// src/pages/UpdateItem.tsx

import React, { useState, useEffect } from 'react'; 
import { useNavigate, useParams } from 'react-router-dom';
import { Form, Button, Alert, Spinner } from 'react-bootstrap';
import { Item } from '../types/item';
import { getItemById, updateItem } from '../api/apiService';
import { useAuth } from '../components/AuthContext';

const UpdateItem: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const { token, logout } = useAuth(); // Include token

  const [formData, setFormData] = useState<Item | null>(null);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});
  const [submissionError, setSubmissionError] = useState<string | null>(null);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [isSubmitting, setIsSubmitting] = useState<boolean>(false);

  // Fetch the item details when the component mounts
  useEffect(() => {
    const fetchItem = async () => {
      if (!token) {
        setSubmissionError('User is not authenticated. Please log in.');
        setLoading(false);
        return;
      }

      try {
        const data: Item = await getItemById(Number(id), token); // Pass the token
        if (!data) {
          setSubmissionError('Item not found.');
          console.warn(`Item with ID ${id} not found.`);
        } else {
          setFormData(data);
        }
      } catch (error: any) {
        console.error('Fetch error:', error);
        if (
          error.response?.status === 401 ||
          error.message === 'Unauthorized' ||
          error.message === 'Invalid token.' ||
          error.message === 'jwt expired' ||
          error.message === 'User is not authenticated. Please log in.'
        ) {
          // Token might be invalid or expired
          logout(); // Clear authentication state
          navigate('/login'); // Redirect to login page
        } else if (error.response?.status === 404) {
          setSubmissionError('Item not found.');
        } else {
          setSubmissionError(`Failed to fetch the item: ${error.message}`);
        }
      } finally {
        setLoading(false);
      }
    };

    fetchItem();
  }, [id, token, logout, navigate]);

  // Handle input changes
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type } = e.target;

    setFormData((prev) =>
      prev
        ? {
            ...prev,
            [name]: type === 'number' && value !== '' ? parseFloat(value) : value,
          }
        : null
    );

    // Clear the specific field error on change
    setErrors((prevErrors) => ({
      ...prevErrors,
      [name]: '',
    }));
  };

  // Validate the form data
  const validateForm = () => {
    const newErrors: { [key: string]: string } = {};

    if (!formData?.name.trim()) newErrors.name = 'Name is required.';
    if (!formData?.category.trim()) newErrors.category = 'Category is required.';
    if (!formData?.countryOfOrigin.trim())
      newErrors.countryOfOrigin = 'Country of origin is required.';
    if (!formData?.countryOfProvenance.trim())
      newErrors.countryOfProvenance = 'Country of provenance is required.';

    const nutritionalFields: Array<keyof Item> = [
      'energy',
      'carbohydrates',
      'sugar',
      'protein',
      'fat',
      'saturatedfat',
      'unsaturatedfat',
      'fibre',
      'salt',
    ];

    nutritionalFields.forEach((field) => {
      const value = formData ? formData[field] : undefined;

      // Type checking: Ensure value is a number before using isNaN
      if (typeof value !== 'number' || isNaN(value)) {
        const fieldName = formatFieldName(field);
        newErrors[field] = `${fieldName} must have a valid numerical value.`;
      } else if (value < 0) {
        const fieldName = formatFieldName(field);
        newErrors[field] = `${fieldName} cannot be negative.`;
      }
    });

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setSubmissionError(null);
    setSuccessMessage(null);

    if (!validateForm() || !formData) return;

    if (!token) {
      setSubmissionError('User is not authenticated. Please log in.');
      return;
    }

    setIsSubmitting(true); // Start submission loading state
    try {
      await updateItem(Number(id), formData, token); // Pass the token

      setSuccessMessage('Item updated successfully!');
      // Optionally, navigate to another page after a short delay
      setTimeout(() => navigate('/'), 2000); // Redirect after 2 seconds
    } catch (error: any) {
      console.error('Update error:', error);
      if (
        error.response?.status === 401 ||
        error.message === 'Unauthorized' ||
        error.message === 'Invalid token.' ||
        error.message === 'jwt expired' ||
        error.message === 'User is not authenticated. Please log in.'
      ) {
        // Token might be invalid or expired
        logout(); // Clear authentication state
        navigate('/login'); // Redirect to login page
      } else if (error.response?.status === 400 && error.response.data) {
        // Handle validation errors from the backend
        const backendErrors = error.response.data.errors;
        if (backendErrors) {
          const formattedErrors: { [key: string]: string } = {};
          Object.keys(backendErrors).forEach((key) => {
            formattedErrors[key] = backendErrors[key].join(' ');
          });
          setErrors(formattedErrors);
          setSubmissionError('Please correct the highlighted errors.');
        } else {
          setSubmissionError(`Failed to update the item: ${error.message}`);
        }
      } else if (error.response?.status === 404) {
        setSubmissionError('Item not found.');
      } else {
        setSubmissionError(`Failed to update the item: ${error.message}`);
      }
    } finally {
      setIsSubmitting(false); // End submission loading state
    }
  };

  // Helper function to display a formatted field name
  const formatFieldName = (field: string) => {
    return field
      .replace(/([A-Z])/g, ' ') // Insert space before capital letters
      .replace(/^./, (str) => str.toUpperCase()); // Capitalize first letter
  };

  // Render loading state
  if (loading)
    return (
      <div className="text-center" style={{ marginTop: '50px' }}>
        <Spinner animation="border" />
        <p>Loading item details...</p>
      </div>
    );

  // Render submission error with a "Go Back" option
  if (submissionError)
    return (
      <div
        className="d-flex justify-content-center align-items-center"
        style={{ minHeight: '100vh', backgroundColor: '#f8f9fa' }}
      >
        <Alert variant="danger" className="w-50 text-center">
          {submissionError}
          <div className="mt-3">
            <Button variant="primary" onClick={() => navigate('/')}>
              Go Back
            </Button>
          </div>
        </Alert>
      </div>
    );

  // Render not found message if formData is null
  if (!formData)
    return (
      <div
        className="d-flex justify-content-center align-items-center"
        style={{ minHeight: '100vh', backgroundColor: '#f8f9fa' }}
      >
        <Alert variant="warning" className="w-50 text-center">
          Item not found.
          <div className="mt-3">
            <Button variant="primary" onClick={() => navigate('/')}>
              Go Back
            </Button>
          </div>
        </Alert>
      </div>
    );

  return (
    <>
      {/* Inject CSS to remove number input spinners */}
      <style>
        {`
          /* Chrome, Safari, Edge, Opera */
          input[type=number]::-webkit-outer-spin-button,
          input[type=number]::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
          }

          /* Firefox */
          input[type=number] {
            -moz-appearance: textfield;
          }
        `}
      </style>

      <div
        className="d-flex justify-content-center align-items-center"
        style={{ minHeight: '100vh', backgroundColor: '#f8f9fa' }}
      >
        <div className="card p-4 shadow" style={{ width: '600px' }}>
          <h1 className="text-center mb-4">Update Item</h1>

          {/* Success Message */}
          {successMessage && <Alert variant="success">{successMessage}</Alert>}

          {/* Submission Error Message */}
          {submissionError && <Alert variant="danger">{submissionError}</Alert>}

          <Form onSubmit={handleSubmit}>
            {/* Name Field */}
            <Form.Group className="mb-3">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                name="name"
                value={formData.name || ''}
                onChange={handleChange}
                isInvalid={!!errors.name}
                placeholder="Enter item name"
              />
              <Form.Control.Feedback type="invalid">
                {errors.name}
              </Form.Control.Feedback>
            </Form.Group>

            {/* Category Field */}
            <Form.Group className="mb-3">
              <Form.Label>Category</Form.Label>
              <Form.Control
                type="text"
                name="category"
                value={formData.category || ''}
                onChange={handleChange}
                isInvalid={!!errors.category}
                placeholder="Enter category"
              />
              <Form.Control.Feedback type="invalid">
                {errors.category}
              </Form.Control.Feedback>
            </Form.Group>

            {/* Certificate Field */}
            <Form.Group className="mb-3">
              <Form.Label>Certificate</Form.Label>
              <Form.Control
                type="text"
                name="certificate"
                value={formData.certificate || ''}
                onChange={handleChange}
                placeholder="Enter certificate (optional)"
              />
            </Form.Group>

            {/* Image URL Field */}
            <Form.Group className="mb-3">
              <Form.Label>Image URL</Form.Label>
              <Form.Control
                type="text"
                name="imageUrl"
                value={formData.imageUrl || ''}
                onChange={handleChange}
                placeholder="Enter image URL (optional)"
              />
            </Form.Group>

            <h2 className="text-center">Nutritional Information per 100g</h2>

            {/* Nutritional Fields */}
            {[
              'energy',
              'carbohydrates',
              'sugar',
              'protein',
              'fat',
              'saturatedfat',
              'unsaturatedfat',
              'fibre',
              'salt',
            ].map((field) => (
              <Form.Group className="mb-3" key={field}>
                <Form.Label>{formatFieldName(field)}</Form.Label>
                <Form.Control
                  type="number"
                  name={field}
                  value={formData[field as keyof Item] ?? ''}
                  onChange={handleChange}
                  isInvalid={!!errors[field]}
                  min="0"
                  step="any"
                  placeholder={`Enter ${formatFieldName(field)}`}
                />
                <Form.Control.Feedback type="invalid">
                  {errors[field]}
                </Form.Control.Feedback>
              </Form.Group>
            ))}

            {/* Country of Origin Field */}
            <Form.Group className="mb-3">
              <Form.Label>Country of Origin</Form.Label>
              <Form.Control
                type="text"
                name="countryOfOrigin"
                value={formData.countryOfOrigin || ''}
                onChange={handleChange}
                isInvalid={!!errors.countryOfOrigin}
                placeholder="Enter country of origin"
              />
              <Form.Control.Feedback type="invalid">
                {errors.countryOfOrigin}
              </Form.Control.Feedback>
            </Form.Group>

            {/* Country of Provenance Field */}
            <Form.Group className="mb-3">
              <Form.Label>Country of Provenance</Form.Label>
              <Form.Control
                type="text"
                name="countryOfProvenance"
                value={formData.countryOfProvenance || ''}
                onChange={handleChange}
                isInvalid={!!errors.countryOfProvenance}
                placeholder="Enter country of provenance"
              />
              <Form.Control.Feedback type="invalid">
                {errors.countryOfProvenance}
              </Form.Control.Feedback>
            </Form.Group>

            {/* Submit and Cancel Buttons */}
            <div className="d-flex justify-content-between align-items-center">
              <Button
                variant="primary"
                type="submit"
                disabled={isSubmitting}
                className="create-button"
                style={{ width: '130px', height: '40px' }}
              >
                {isSubmitting ? (
                  <>
                    <Spinner as="span" animation="border" size="sm" /> Updating...
                  </>
                ) : (
                  'Update Item'
                )}
              </Button>
              <Button
                variant="secondary"
                onClick={() => navigate('/')}
                style={{ width: '130px', height: '40px' }}
              >
                Cancel
              </Button>
            </div>
          </Form>
        </div>
      </div>
    </>
  );
};

export default UpdateItem;
